.drum-button {
    padding: 15px 25px;
    font-size: 24px;
    text-align: center;
    cursor: pointer;
    outline: none;
    color: #fff;
    background-color: grey;
    border: none;
    border-radius: 15px;
    box-shadow: black 3px 3px 5px;
    width: calc(100% / 3 - 15px);
    height: calc(100% / 3 - 10px);
    display: flex;
    justify-content: center;
    align-items: center;
    color: black;
}

.drum-button:active {
    background-color: orange;
    box-shadow: 0 5px #666;
    transform: translateY(4px);
}